html, body {
  background: black;
  color: #00b800;
  margin:0;
  padding: 0;
  padding-top: 10px;
  font-family: 'Press Start 2P', cursive;
  text-align: center;
}

body {
  font-weight: 400;
}

.App {
  max-width: 900px;
  margin: 0 auto;
}

button {
  font-family: 'Press Start 2P', cursive;
  display: block;
  margin: 2em auto;
  border: none;
  background: #00b800;
  padding: 10px 20px;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 15px;
  letter-spacing: 2px;
}

h1 {
  font-size: 20px;
  text-align:center;
}

textarea {
  font-family: 'Press Start 2P', cursive;
  border: 3px solid black;
  width: 90%;
  margin: 0 auto;
  outline: none;
  height: 200px;
  background: #00b800;
  padding: 10px;
}

textarea:disabled,
button:disabled {
  background: #424242;
  color: #acacac;
  cursor: not-allowed;
}
